.gallery-page {
  padding: 5rem 2rem;
  max-width: 1200px;
  margin: 0 auto;
}

/* ğŸ§¸ í—¤ë” */
.gallery-header {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  justify-content: center;
  margin-bottom: 0.5rem;
}
.gallery-title {
  font-size: 1.5rem;
  font-weight: 800;
  padding: 1rem 1rem 0.5rem;
  text-align: center;
  color: #222;
  line-height: 1.4;
}
.gallery-icon { font-size: 1.6rem; }
.gallery-description {
  text-align: center;
  font-size: 1rem;
  color: #666;
  margin-bottom: 2rem;
}

/* ğŸ“‹ í•„í„° */
.gallery-filter {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin: 1.5rem 0;
  flex-wrap: wrap;
}
.gallery-filter select {
  appearance: none;
  background-color: #fff7f0;
  border: 2px solid #ffb6c1;
  border-radius: 1rem;
  padding: 0.6rem 1.2rem;
  font-size: 1rem;
  font-family: 'HancomMalangMalang-Regular', sans-serif;
  color: #ff5d8f;
  box-shadow: 2px 2px 6px rgba(255, 182, 193, 0.2);
  cursor: pointer;
  transition: all 0.3s ease;
  text-align: center;
}
.gallery-filter select:hover {
  background-color: #ffe4ec;
  border-color: #ff85a2;
  transform: scale(1.05);
}
.gallery-filter select:focus {
  outline: none;
  box-shadow: 0 0 0 3px rgba(255, 192, 203, 0.4);
}

/* ğŸ“· ê°¤ëŸ¬ë¦¬ ê·¸ë¦¬ë“œ â€” ëª¨ë°”ì¼ 1ì—´ / íƒœë¸”ë¦¿ 2ì—´ / ë°ìŠ¤í¬íƒ‘ 3ì—´ */
.gallery-gallery {
  display: grid;
  grid-template-columns: 1fr;     /* ê¸°ë³¸(ëª¨ë°”ì¼): 1ì—´ */
  gap: 1.8rem;
  padding: 0 1rem;
}
@media (min-width: 600px) {
  .gallery-gallery { grid-template-columns: repeat(2, 1fr); } /* 2ì—´ */
}
@media (min-width: 1024px) {
  .gallery-gallery { grid-template-columns: repeat(3, 1fr); } /* 3ì—´ */
}

/* ğŸ“¦ ì¹´ë“œ */
.gallery-card {
  background: #ffffffd9;
  border-radius: 1.2rem;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.06);
  overflow: hidden;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  backdrop-filter: blur(2px);
  padding: 0; /* ì¹´ë“œ ë¹„ìœ¨/êµ¬ì¡°ëŠ” ìœ ì§€ */
}
.gallery-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 28px rgba(0, 0, 0, 0.08);
}

/* â¬†ï¸ ì œëª© */
.gallery-title-card {
  font-family: 'HancomMalangMalang-Regular';
