  padding: 0;
  font-family: Arial, sans-serif;
  background: linear-gradient(to bottom, #fff8cc, #ffffff);
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: cover;
  color: #2c3e50;
  text-align: center;
  overflow-x: hidden;
  overflow-y: scroll;
  scroll-snap-type: y mandatory;     /* í˜ì´ì§€ ë‹¨ìœ„ ìŠ¤ëƒ… */
  scroll-behavior: smooth;
  scroll-padding-top: var(--nav-h);  /* ìŠ¤ëƒ… ì •ì§€ì  ë³´ì • */
}

.fullpage-wrapper { width: auto; }

.page {
