/* ===================== Intro Common ===================== */
.intro-section {
  min-height: 100svh;
  height: auto;
  padding: 4rem 2rem 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  scroll-margin-top: var(--nav-h);
}

.intro-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 0 3rem 3rem;
  border-radius: 2rem;
  text-align: center;
}

.subheading {
  font-family: 'EF_jejudoldam', sans-serif;
  font-weight: 600;
  font-size: 2.7rem;
  color: #24292e;
  margin: 0 0 1rem;
  text-align: center;
}

.intro-left-align { text-align: left !important; margin-left: 0 !important; padding-left: 0 !important; }

.intro-title {
  font-family: 'Cafe24Meongi', sans-serif;
  font-size: 2rem;
  margin-bottom: 0.5rem;
  color: #e74c3c;
}

.intro-text {
  font-family: 'S-CoreDream-3Light', sans-serif;
  line-height: 1.6;
  color: #2c3e50;
  margin: 2rem 0;
}

/* ===================== HERO (1í˜ì´ì§€) ===================== */
.intro-hero{
  min-height: 100svh;
  display: grid;
  /* [ìœ—ê³µê°„][WELCOME][ì‹œë£¨ì˜ í•˜ë£¨][ì´ë¯¸ì§€][ì„¤ëª…][ì•„ë«ê³µê°„] */
  grid-template-rows: 1fr auto auto auto auto 1fr;
  justify-items: center;
  padding: 0 16px;
}
.intro-hero .subheading{
  grid-row: 2;
  align-self: end;
  margin: 4px 0 0;
}
.intro-hero .intro-title{
  grid-row: 3;
  align-self: end;
  width: 100%;
  text-align: left;
  padding-left: 12px;
  margin: 0 0 8px;
}
.intro-banner-container{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
  padding: 1rem 0;
}
.intro-hero .intro-banner-container{ grid-row: 4; margin: 0; }
.intro-banner{
  width: 100%;
  height: auto;
  display: block;
  object-fit: contain;
  margin: 0; padding: 0; border-radius: 0;
}
.intro-hero .intro-banner{ width: min(88vw, 418px); }
.intro-hero .intro-desc{
  grid-row: 5;
  align-self: start;
  margin-top: 24px;
  text-align: center;
  font-family: 'S-CoreDream-3Light', sans-serif;
  color: #373738ff;
  font-size: 1.1rem;
}

/* ===================== Speech Bubble ===================== */
.siru-speech-bubble {
  position: relative;
  display: inline-block;
  background: #ffffff;
  color: #2c3e50;
  border-radius: 999px;
  padding: 0.8rem 2rem;
  font-family: 'Cafe24Meongi', sans-serif;
  font-size: 1rem;
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.1);
  max-width: 500px;
  animation: floatTalk 2s ease-in-out infinite;
  text-align: center;
}
.siru-speech-bubble::after {
  content: "";
  position: absolute;
  bottom: -12px; left: 50px; width: 0; height: 0;
  border-left: 12px solid transparent; border-right: 12px solid transparent; border-top: 12px solid #ffffff;
}
@keyframes floatTalk { 0%, 100% { transform: translateY(0); } 50% { transform: translateY(-4px); } }

/* ===================== Instagram ===================== */
.instagram-section { padding: 2rem 1rem; text-align: center; background: #fffdf4; }
.instagram-section h4 { font-family: 'MonoplexKR-Regular', sans-serif; margin-bottom: 1rem; color: #2c3e50; }
.instagram-link-new a {
  display: inline-flex; align-items: center; gap: 0.5rem;
  padding: 0.75rem 1.2rem; margin-top: 0; border: 1.5px solid #e1306c; border-radius: 6px;
  color: #e1306c; font-weight: 600; font-size: 1rem; font-family: 'S-CoreDream-6Bold', sans-serif;
  transition: all 0.3s ease; text-decoration: none; box-shadow: 0 2px 8px rgba(0, 0, 0, 0.04);
}
.instagram-link-new a:hover { background: #e1306c; color: #ffffff; box-shadow: 0 4px 12px rgba(225, 48, 108, 0.2); }
.instagram-icon { width: 1.3rem; height: 1.3rem; }

/* ===================== Dog Info Table (ê¸°ë³¸) ===================== */
.dog-profile-title {
  font-size: 1.5rem;
  font-family: 'CoreDream', sans-serif;
  color: #141414;
  margin: 0 0 0.6rem;      /* ì œëª© ë” ìœ„ë¡œ */
  text-align: center;
}
.dog-specs {
  width: 100%;
  background: #fff0f5;
  border-radius: 1.5rem;
  border: 2px solid #ffc4d6;
  box-shadow: 0 4px 16px rgba(255, 105, 180, 0.15);
  overflow: hidden;
  font-family: 'S-CoreDream-3Light', sans-serif;
  border-spacing: 0;
  margin: 0 auto;
}
.dog-specs th {
  background: #ffe2ec;
