/* ===================== SECTION 2 (ì¤‘ì•™ + %ë¹„ìœ¨ + í‘œ ë¹„ë¡€ ì¶•ì†Œ) ===================== */
.intro-section.section-2 { padding-left: 0; padding-right: 0; } /* ì¢Œìš° íŒ¨ë”© ë¹„ëŒ€ì¹­ ë°©ì§€ */

/* ì„¹ì…˜ ë†’ì´ë¥¼ ëª…í™•íˆ: í¼ì„¼íŠ¸ í–‰ì´ ì •ìƒ ê³„ì‚°ë˜ë„ë¡ */
.section-2{
  display: grid;
  height: calc(100svh - var(--nav-h));
  min-height: calc(100svh - var(--nav-h));
  margin-top: var(--nav-h);
  padding: 0;
  place-items: stretch;
  justify-content: center;
  justify-items: center;
}

/* 7í–‰ ê·¸ë¦¬ë“œ â€” í‘œ í–‰ì€ ìµœì†Œ ë†’ì´ ë³´ì¥(ì‚¬ë¼ì§ ë°©ì§€) */
.section-2 .sec2-grid{
  height: 100%;
  width: min(800px, 100%);
  margin-inline: auto;
  padding-inline: clamp(12px, 3vw, 20px);
  row-gap: 10px;
  box-sizing: border-box;
  justify-self: center;

  display: grid;
  grid-template-rows:
    var(--sec2-topP)                    /* ìœ„ ìŠ¤í˜ì´ì„œ(%) */
    var(--sec2-titleP)                  /* ì œëª©(%) */
    minmax(220px, var(--sec2-tableP))  /* â˜… í‘œ(%) + ìµœì†Œ 220px */
    auto                                /* misc(ë‚´ìš©ë§Œí¼) */
    var(--sec2-bottomP)                 /* ì•„ë˜ ìŠ¤í˜ì´ì„œ(%) */
    min-content                         /* í‘¸í„° */
    1rem;                               /* ë°”ë‹¥ ì—¬ë°± */
}

/* ìì‹ë“¤ì´ ì¤„ì–´ë“¤ ìˆ˜ ìˆê²Œ(ì¶•ì†Œ/ìŠ¤í¬ë¡¤ ì •ìƒë™ì‘) */
.section-2 .sec2-grid > * { min-height: 0; }

/* ëª…ì‹œì  ë°°ì¹˜ */
.sec2-title { grid-row: 2; }
.sec2-specs { grid-row: 3; }
.sec2-misc  { grid-row: 4; }
.section-2 > .sec2-grid > footer {
  grid-row: 6;
  align-self: end;
  width: 100%;
  padding: 0;
}

/* === í‘œ ë˜í¼: ì¤‘ì•™ ì •ë ¬ + ë¹„ë¡€ ì¶•ì†Œ ì»¨í…Œì´ë„ˆ === */
.sec2-specs{
  overflow: hidden;
  container-type: size;              /* cqh/cqw ì‚¬ìš© */
  display: grid;
  place-items: start center;
  padding: 8px;
}

/* í‘œ ìì²´: í­ ì œí•œ + ê· ë“± ì—´í­ */
.sec2-specs .dog-specs{
  width: min(600px, 100%);           /* ê¸°ë³¸: ì¡°ê¸ˆ í¬ê²Œ */
  margin-inline: auto;
  table-layout: fixed;
}
.sec2-specs .dog-specs th,
.sec2-specs .dog-specs td{ width: 50%; }

/* ì»¨í…Œì´ë„ˆ ë†’ì´ì— ë¹„ë¡€í•´ì„œ ê¸€ì/íŒ¨ë”©/í–‰ë†’ì´ ìŠ¤ì¼€ì¼ */
.sec2-specs .dog-specs,
.sec2-specs .dog-specs th,
