/* misc: ë‚´ìš©ë§Œí¼, ë„˜ì¹˜ë©´ ë‚´ë¶€ ìŠ¤í¬ë¡¤(í•„ìš”ì‹œ ìœ ì§€) */
.sec2-misc{
  display: flex;
