/* ===== ì»¨í…Œì´ë„ˆì¿¼ë¦¬ ë¯¸ì§€ì› ë¸Œë¼ìš°ì € í´ë°± ===== */
@supports not (height: 1cqh){
  .sec2-specs{ overflow: auto; } /* êµ¬í˜•ì€ ìŠ¤í¬ë¡¤ë¡œ í´ë°± */
  .sec2-specs .dog-specs{ width: min(600px, 100%); }
